          justify-content: space-between;
  width: 100%; }
  .dropdown-wrapper .dropdown {
    position: relative;
    display: inline-block;
    color: #b4b4b4;
    margin-right: 32px; }
    .dropdown-wrapper .dropdown input[type=checkbox] {
      display: none; }
    .dropdown-wrapper .dropdown label {
      position: relative;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
